<template>
  <div class="roles">
    <el-breadcrumb separator="/">
      <el-breadcrumb-item :to="{ path: '/home' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>权限管理</el-breadcrumb-item>
      <el-breadcrumb-item>角色列表</el-breadcrumb-item>
    </el-breadcrumb>
    <el-card>
      <div class="roles-top">
        <el-button type="primary" @click="add">添加角色</el-button>
      </div>
      <div class="roles-bottom">
        <el-table
          :data="roles"
          stripe
          style="width: 100%"
          border
          v-if="roles.length"
        >
          <el-table-column type="expand" class="expands">
            <template v-slot="scope">
              <el-row
                v-for="(item, i1) in scope.row.children"
                :key="item.id"
                :class="['bdbottom', i1 === 0 ? 'bdtop' : '', 'vcenter']"
              >
                <el-col :span="5">
                  <el-tag closable @close="handleClose(item.id, scope.row.id)">
                    {{ item.authName }}
                  </el-tag>
                  <i class="el-icon-caret-right"></i>
                </el-col>
                <el-col :span="19">
                  <el-row
                    v-for="(item2, i2) in item.children"
                    :key="item2.id"
                    :class="['bdbottom', i2 === 0 ? 'bdtop' : '', 'vcenter']"
                  >
                    <el-col :span="6">
                      <el-tag
                        closable
                        type="success"
                        @close="handleClose(item2.id, scope.row.id)"
                      >
                        {{ item2.authName }}
                      </el-tag>
                      <i class="el-icon-caret-right"></i>
                    </el-col>
                    <el-col :span="18">
                      <el-tag
                        type="danger"
                        v-for="item3 in item2.children"
                        :key="item3.id"
                        closable
                        @close="handleClose(item3.id, scope.row.id)"
                      >
                        {{ item3.authName }}
                      </el-tag>
                    </el-col>
                  </el-row>
                </el-col>
              </el-row>
            </template>
          </el-table-column>
          <el-table-column label="#" width="80" type="index"> </el-table-column>
          <el-table-column prop="roleName" label="角色名称"> </el-table-column>
          <el-table-column prop="roleDesc" label="角色描述"> </el-table-column>
          <el-table-column label="操作">
            <template v-slot="scope">
              <el-button
                type="primary"
                icon="el-icon-edit"
                size="mini"
                @click="edit(scope.row)"
                >编辑</el-button
              >
              <el-button
                type="danger"
                icon="el-icon-delete"
                size="mini"
                @click="del(scope.row.id)"
                >删除</el-button
              >
              <el-button
                type="warning"
                icon="el-icon-s-tools"
                size="mini"
                @click="privileges(scope.row)"
                >分配权限</el-button
              >
            </template>
          </el-table-column>
        </el-table>
      </div>
    </el-card>
    <!-- 添加编辑对话框 -->
    <RoleDialog :dialogVisible.sync="dialogVisible" :ruleForm="ruleForm" />
    <!-- 树形控间 -->
    <el-dialog
      title="分配权限"
      :visible.sync="treeVisible"
      width="50%"
      @close="setRightDialogClosed"
    >
      <el-tree
        :data="allRights"
        :props="props"
        show-checkbox
        node-key="id"
        default-expand-all
        :default-checked-keys="defKeys"
        ref="treeRef"
      ></el-tree>
      <span slot="footer" class="dialog-footer">
        <el-button @click="treeVisible = false">取 消</el-button>
        <el-button type="primary" @click="sure">确 定</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import RoleDialog from "@/components/RoleDialog.vue";
import { mapState, mapActions } from "vuex";
export default {
  components: { RoleDialog },
  data() {
    return {
      dialogVisible: false,
      ruleForm: {
        roleName: "",
        roleDesc: "",
      },
      props: {
        label: "authName",
        children: "children",
      },
      treeVisible: false,
      defKeys: [],
      roleId: "",
    };
  },
  created() {
    this.getRoles();
  },
  computed: {
    ...mapState({
      roles: (state) => state.roles.roles,
      allRights: (state) => state.roles.allRights,
    }),
  },
  methods: {
    // 添加角色
    add() {
      this.dialogVisible = true;
      this.ruleForm = { roleName: "", roleDesc: "" };
    },
    // 删除角色
    del(id) {
      this.$confirm("此操作将永久删除该文件, 是否继续?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning",
      })
        .then(() => {
          this.delRoles(id).then(
            (res) => {
              this.$message({
                type: "success",
                message: "删除成功!",
              });
            },
            (error) => {
              this.$message({
                type: "error",
                message: error,
              });
            }
          );
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "已取消删除",
          });
        });
    },
    // 编辑角色
    edit(row) {
      this.dialogVisible = true;
      this.ruleForm = { ...row };
    },
    // 删除tab
    handleClose(rightId, roleId) {
      this.$confirm("此操作将永久删除该文件, 是否继续?", "提示", {
        confirmButtonText: "确定",
        cancelButtonText: "取消",
        type: "warning",
      })
        .then(() => {
          this.delRolesLimits({ rightId, roleId }).then(
            (res) => {
              this.$message({
                type: "success",
                message: "删除成功!",
              });
            },
            (error) => {
              this.$message({
                type: "error",
                message: error,
              });
            }
          );
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: "已取消删除",
          });
        });
    },
    // 获取已有权限
    getReadyRights(node, arr) {
      if (!node.children) {
        return arr.push(node.id);
      }
      node.children.forEach((item) => this.getReadyRights(item, arr));
    },
    // 分配权限
    privileges(row) {
      this.getAllRights("tree");
      this.treeVisible = true;
      this.roleId = row.id;
      this.getReadyRights(row, this.defKeys);
    },
    setRightDialogClosed() {
      this.defKeys = [];
    },
    // 分配权限保存
    sure() {
      this.treeVisible = false;
      const keys = [
        ...this.$refs.treeRef.getCheckedKeys(),
        ...this.$refs.treeRef.getHalfCheckedKeys(),
      ].join(",");
      this.roleAuthorization({ id: this.roleId, rids: keys }).then(
        () => {
          this.$message.success("分配权限成功!");
        },
        (error) => {
          this.$message.error(error);
        }
      );
    },
    ...mapActions([
      "getRoles",
      "delRoles",
      "delRolesLimits",
      "getAllRights",
      "roleAuthorization",
    ]),
  },
};
</script>

<style lang="less">
.roles {
  .el-breadcrumb {
    font-size: 12px;
    margin-bottom: 20px;
  }
  .roles-top {
    .el-button {
      margin-bottom: 15px;
    }
  }
  .el-row {
    padding: 15px;
  }
  .expands {
    padding: 0 20px;
  }
  .bdtop {
    border-top: 1px solid #eee;
  }

  .bdbottom {
    border-bottom: 1px solid #eee;
  }
  .bd-left {
    border-left: 1px solid #eee;
  }
  .vcenter {
    display: flex;
    align-items: center;
  }
  .el-tag {
    margin: 7px;
  }
  .el-table {
    font-size: 12px;
  }
}
</style>
