<template>
  <div class="roles-dialog">
    <el-dialog
      :title="ruleForm.id ? '编辑角色' : '添加角色'"
      :visible="dialogVisible"
      width="50%"
      @close="$emit('update:dialogVisible', false)"
    >
      <el-form
        :model="ruleForm"
        :rules="rules"
        ref="form"
        label-width="100px"
        class="demo-ruleForm"
      >
        <el-form-item label="角色名称" prop="roleName">
          <el-input v-model="ruleForm.roleName"></el-input>
        </el-form-item>
        <el-form-item label="角色描述" prop="roleDesc">
          <el-input v-model="ruleForm.roleDesc"></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="$emit('update:dialogVisible', false)"
          >取 消</el-button
        >
        <el-button type="primary" @click="sure">确 定</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import { mapActions } from "vuex";
export default {
  props: ["dialogVisible", "ruleForm"],
  data() {
    return {
      rules: {
        roleName: {
          required: true,
          message: "请输入角色名称",
          trigger: "blur",
        },
        roleDesc: {
          required: true,
          message: "请输入角色描述",
          trigger: "blur",
        },
      },
    };
  },
  methods: {
    sure() {
      this.$refs.form.validate((valida) => {
        if (valida) {
          if (this.ruleForm.id) {
            this.editRoles(this.ruleForm).then(
              () => {
                this.$message.success("编辑成功");
              },
              (error) => {
                this.$message.error(error);
              }
            );
          } else {
            // 添加
            this.addRoles(this.ruleForm).then(
              () => {
                this.$message.success("添加成功");
              },
              (error) => {
                this.$message.error(error);
              }
            );
          }
        }
        this.$emit("update:dialogVisible", false);
      });
    },
    ...mapActions(["addRoles", "editRoles"]),
  },
};
</script>

<style></style>
